.App {
  max-width: 1000px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0px auto;
}

.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.App-header h1 {
  /* font-size: 5vw; */

}

.App-header h3 {
  /* font-size: 2.5vw; */
  margin-bottom: 10px;
}

.App-header button {
  border: none;
  color: white;
  cursor: pointer;
  background-color: #346;
  border-radius: 3px;
  padding: 5px 20px;
  font-size: 1em;
  line-height: 23px;
}

.Difficulty {
  width: 35%;
  text-align: center;
}

.Difficulty-chooser {
  display: flex;
  justify-content: space-between;
}

.Difficulty-chooser i.material-icons {
  /* font-size: 3vw; */
}

.Board {
  display: flex;
  flex-direction: row;
  border: 5px solid white;
  border-radius: 10px;
  flex-wrap: wrap;
  width: min(85vw, 75vh, 700px);
  height: min(85vw, 75vh, 700px);
  margin: 20px auto;
  user-select: none;
}

.Board-Element {
  border-right: 1px solid white;
  border-bottom: 1px solid white;
  display: flex;
  width: calc((100% - 16px) / 9);
  height: calc((100% - 16px) / 9);
  align-items: center;
  justify-content: center;
  font-size: 200%;
  cursor: pointer;
}

.Board-Element.disabled {
  background-color: #333;
  /* cursor: not-allowed; */
}

.Board-Element.selected {
  background-color: #181;
  color: #000;
}
.Board-Element.highlight {
  background-color: #181;
}

.Board-Element:nth-child(3n) {
  border-right-width: 5px;
}

.Board-Element:nth-child(9n) {
  border-right: none;
}

.Board-Element:nth-child(n+19):nth-child(-n+27) {
  border-bottom-width: 5px;
}

.Board-Element:nth-child(n+46):nth-child(-n+54) {
  border-bottom-width: 5px;
}

.Board-Element:nth-child(n+73) {
  border-bottom: none;
}

