.Actions {
  width: min(85vw, 75vh, 700px);
  margin: 20px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.Actions button {
  cursor: pointer;
  font-size: 1.5em;
  background-color: white;
  color: black;
  border: none;
  border-radius: 5px;
  width: 10%;
  justify-content: center;
  align-content: center;
  display: flex;
  margin: 2% 10% 0 0;
  line-height: 150%;
}

.Actions button:nth-child(5n) {
  margin-right: 0;
}

.About {
  margin: 0px auto;
  width: 75%;
}
